// src/components/wedding/GoldenParticles.jsx
import React, { useState, useEffect, useMemo } from 'react';

export default function GoldenParticles({ isTransitioning }) {
  const [particleCount, setParticleCount] = useState(isTransitioning ? 300 : 20);

  useEffect(() => {
    if (isTransitioning) {
      setParticleCount(300);
      const reductionSteps = [
        { count: 150, delay: 2000 },
        { count: 50, delay: 3000 },
        { count: 20, delay: 4000 }
      ];

      const timers = reductionSteps.map(({ count, delay }) =>
        setTimeout(() => setParticleCount(count), delay)
      );

      return () => timers.forEach(timer => clearTimeout(timer));
    }
  }, [isTransitioning]);

  const particles = useMemo(() => 
    [...Array(particleCount)].map(() => ({
      size: Math.random() * 4 + 2,
      left: `${Math.random() * 100}%`,
      top: `${Math.random() * 100}%`,
      delay: Math.random() * 0.5,
      duration: Math.random() * 1.5 + 1.5,
    })), [particleCount]
  );

  return (
    <div style={{
      position: 'fixed',
      inset: 0,
      pointerEvents: 'none',
      zIndex: 40,
      perspective: '1000px'
    }}>
      <div style={{
        position: 'absolute',
        inset: 0,
        opacity: isTransitioning ? 0.8 : 0.3,
        transition: 'opacity 1.5s ease-out',
      }}>
        {particles.map((particle, i) => (
          <div
            key={i}
            style={{
              position: 'absolute',
              width: `${particle.size}px`,
              height: `${particle.size}px`,
              backgroundColor: 'rgba(251, 191, 36, 0.8)',
              borderRadius: '50%',
              left: particle.left,
              top: particle.top,
              animation: `sparkleFloat ${particle.duration}s infinite`,
              animationDelay: `${particle.delay}s`,
              willChange: 'transform, opacity',
              transform: 'translateZ(0)',
            }}
          />
        ))}
      </div>
      <style>{`
        @keyframes sparkleFloat {
          0% {
            transform: translate3d(0, 0, 0) scale(1);
            opacity: 1;
          }
          50% {
            transform: translate3d(${Math.random() * 30 - 15}px, 
                                 ${Math.random() * -50 - 25}px, 
                                 ${Math.random() * 30}px) 
                      scale(1.2);
            opacity: 0.8;
          }
          100% {
            transform: translate3d(0, 0, 0) scale(1);
            opacity: 1;
          }
        }
      `}</style>
    </div>
  );
}